.hero{
    width: 100%;
    min-height: 100vh;
    background-image: linear-gradient(rgba(8,0,58,0.5),rgba(8,0,58,0.5)), url('./img/b1.jpg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
}
.hero-text{
    text-align: center;
    max-width: 800px;
}
.hero-text h1{
    font-size: 40px;
    font-weight: 600;
}
.hero-text p{
    max-width: 700px;
    margin: 10px auto 20px;
    line-height: 1.4;
}